<template>
  <div class="count-box">
    <button @click="handleSub" class="minus">-</button>
    <input :value="value" @change="handleChange" class="inp" type="text">
    <!--  @change  用于检测文本框发生改变（失焦时触发）  -->
    <button @click="handleAdd" class="add">+</button>
  </div>
</template>

<script>
export default {
    props: {
        value: {
            type: Number,
            default: 1
        }
    },
    methods: {
        handleAdd () {
            this.$emit('input', this.value + 1)
        },
        handleSub () {
            if ( this.value < 1 )
                { return }
            this.$emit('input', this.value - 1)
        },
        handleChange (e) {
            // console.log(e.target.value);
            const num = +e.target.value 
    // 如果 value 不是 number，则 +value 返回值为 NaN
            if ( isNaN(num) || num < 0 ) {
                e.target.value = this.value
                return
            }
            this.$emit('input', num)
        }
    }
}
</script>

<style lang="less" scoped>
.count-box {
    width: 110px;
    display: flex;
    .add, .minus {
        width: 30px;
        height: 30px;
        outline: none;
        border: none;
        background-color: #f1f1f1;
    }
    .inp {
        width: 40px;
        height: 30px;
        outline: none;
        border: none;
        margin: 0 5px;
        background-color: #f1f1f1;
        text-align: center;
    }
}

</style>